<template>
  <van-dialog
    v-model="show"
    class="work_flow-linksDialog"
    show-cancel-button
    :before-close="beforeClose"
    title=""
  >
    <van-picker ref="picker" :columns="data" value-key="Name" title="" />
  </van-dialog>
</template>

<script>
export default {
  name: "ChooseLinksDialog",
  data() {
    return {
      data: {},
      show: false,
      title: null,
      resolve: () => {},
    };
  },
  methods: {
    showFun(data, resolve) {
      this.data = (data.SendParams && data.SendParams.Lines) || {};
      this.show = true;
      if (resolve) this.resolve = resolve;
    },
    beforeClose(action, done) {
      if (action !== "cancel") {
        done(false);
        this.resolve(this.$refs.picker.getValues()[0]);
      } else {
        done();
      }
    },
  },
};
</script>

<style></style>
