import NProgress from 'nprogress';
import 'nprogress/nprogress.css';

import "_platform/dsf/utils";
import "../dsf_config";

//
import "_platform/dsf/metadata";
import "../metadata";
//
import bhc from "_platform/dsf/mobile/bhc";
//
import filters from "_platform/dsf/filters";
//
import directives from "_platform/dsf/directives";
//,
import output from '_platform/dsf/output'
import api from '../api';

// 
import "../assets/styles/mobile/dsfStyle.scss";



import hot from './hotload';
//
import App from "./App.vue";
import store from "../store";
import router from "./router";
import Vue from "vue";
// import { first } from 'xe-utils/methods';

NProgress.configure({
  easing: 'ease-in',
  minimum: 0.1,
  showSpinner: false
});

Vue.use(bhc);
Vue.use(filters);
Vue.use(directives);
Vue.use(output);

dsf.init.injectionDsf(Vue, dsf);
dsf.init.injectionHttp(Vue, dsf.http);
dsf.init.injectionWebAPI(Vue, api);

Vue.config.productionTip = false;



window.$$mobile = true;


router.beforeEach((to, from, next) => {
  NProgress.start();
  next();
});

router.afterEach((to, from) => {
  NProgress.done();
});

dsf.init.hotLoad(hot || [], router, 'mobile')
  .then(() => {
    new Vue({
      store,
      router,
      created() {
        //rem
        dsf.flexble();
      },
      render: h => h(App)
    }).$mount("#app");
  });