<template>
  <van-tabbar v-model="active" class="ds-bottom-nav ds_control" :fixed="false" :safe-area-inset-bottom="true">
    <van-tabbar-item v-for="(item,index) in buttons" :key="index" :name="index" @click.stop="go(item.url)">
      <span>{{ item.text }}</span>
      <template v-if="active==index">
        <dsf-icon v-if="item.icon.active" slot="icon" :name="item.icon.active?item.icon.active:'yiwen'"></dsf-icon>
        <dsf-icon v-else-if="item.icon.general" slot="icon" :name="item.icon.general?item.icon.general:'yiwen'"></dsf-icon>
      </template>
      <template v-else>
        <dsf-icon v-if="item.icon.general" slot="icon" :name="item.icon.general?item.icon.general:'yiwen'"></dsf-icon>
      </template>
    </van-tabbar-item>
  </van-tabbar>
</template>
<script>
export default {
  name: 'DsfMobileButtonNav',
  ctrlCaption: '',
  props: {
    buttons: {
      type: Array,
      default() {
        return [
          {
            name: '',//
            route: 'index',//name
            fonticon: 'shouyeweixuanzhong',//
            activefonticon: 'shouyexuanzhong',
            icon: {//
              active: 'https://img.yzcdn.cn/vant/user-active.png',
              inactive: 'https://img.yzcdn.cn/vant/user-inactive.png'
            }
          }
        ]
      }
    }
  },
  data() {
    return {
      active: 0
    }
  },
  computed: {
  },
  watch: {
    $route: function (val) {
      val.meta.level === 1 && this.setActive(val.path);
    }
  },
  beforeCreate() {
  },
  created() {
  },
  beforeMount() {
  },
  mounted() {
  },
  beforeDestroy() {
  },
  destroyed() {
  },
  methods: {
    setActive(path) {
      let index = _.findIndex(this.buttons, function (item) {
        return item.url === path
      });
      if (index >= 0) {
        this.active = index;
      }
    },
    go(url) {
      if (this.$route.path === url) {
        return;
      }
      this.$router.push({
        path: url
      })
    }
  },
}
</script>