import { DsfMobileLoading } from '../common';
const Mask = Vue.extend(DsfMobileLoading);
const toggleLoading = (el, binding) => {
  if (binding.value) {
    Vue.nextTick(() => {
      let position = dsf.getCss(el, 'position');
      (position !== 'absolute' && position !== 'relative' && position !== 'fixed') && (el.style.position =
        'relative')
      // loading
      el.mask.style.display = 'block';
      // 
      insertDom(el, el, binding);
    })
  } else {
    el.mask.style.display = 'none'
  }
}
const insertDom = (parent, el) => {
  parent.appendChild(el.mask)
}

Vue.directive('loading', {
  bind: function(el, binding) {
    const mask = new Mask({
      el: document.createElement('div'),
      data: {
        text: el.getAttribute('load-text') || ''
      }
    })
    el.instance = mask
    el.mask = mask.$el
    binding.value && toggleLoading(el, binding)
  },
  update: function(el, binding) {
    if (binding.oldValue !== binding.value) {
      toggleLoading(el, binding)
    }
  },
  unbind: function(el, binding) {
    el.mask && el.mask.parentNode && el.removeChild(el.mask)
    el.instance && el.instance.$destroy();
  }
})