<template>
  <div id="app">
    <div class="mobile-view">
      <template>
        <div class="mobile-view-content" :class="{'has-foot':$route.meta.level==1}">
          <transition :name="transitionName">
            <keep-alive :include="cachePages">
              <router-view class="full-view" @inited="viewInited"></router-view>
            </keep-alive>
            <!-- <keep-alive>
              <router-view class="full-view" @inited="viewInited"></router-view>
            </keep-alive> -->
          </transition>
        </div>
        <div class="mobile-view-foot">
          <transition :name="transitionName">
            <div v-if="$route.meta.level==1" :key="'nav-bar'" class="mobile-view-nav-bar">
              <DsfMobileButtonNav :buttons="navs"></DsfMobileButtonNav>
              <!-- <router-link :to="{'path':'/mobile/test/wengtest/mobiletest002'}">adsfasfs</router-link> -->
            </div>
            <div v-else :key="'nav-bar-opacity'" class="mobile-view-nav-bar-opacity"></div>
          </transition>
        </div>
      </template>
    </div>
  </div>
</template>
<script>
export default {
  provide() {
    return {
      $isMobile: true,
    }
  },
  data() {
    return {
      themes: dsf.config.defaultTheme,
      transitionName: '', //
      controlName: "router-view",
      cachePages: [],
      navs: [
        {
          url: "/mobile/test/wengtest/mobiletest001",
          text: "1",
          icon: {
            general: "shouyeweixuanzhong",
            active: "shouyexuanzhong"
          }
        },
        {
          url: "/mobile/test/wengtest/mobiletest002",
          text: "2",
          icon: {
            general: "zaixueweixuanzhong",
            active: "zaixuexuanzhong"
          }
        },
        {
          url: "/mobile/test/wengtest/mobiletest003",
          text: "3",
          icon: {
            general: "",
            active: ""
          }
        }
      ]
    }
  },
  watch: {
    $route: {
      handler(to, from) {
        if ((to || !from) || (to.meta.level == 1 && to.meta.level == from.meta.level)) {
          this.transitionName = ""
        }
        else {
          this.transitionName = window.$$state.pageDirection
        }
      },
      immediate: true
    }
  },
  beforeCreate() {
  },
  mounted() {
    //
    document.body.classList.add(this.themes);
  },
  methods: {
    viewInited(view) {
      //
      if (view.$keepAlive === true) {
        //keepAlive
        //activated
        dsf.array.ensure(this.cachePages, view.$options.name);
      }
    }
  }
};
</script>
<style lang="scss" scoped>
#app {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  overflow: hidden;
}
</style>