//
import {
  Field,
  Popup,
  Dialog,
  Image,
  Grid,
  ImagePreview,
  Loading,
  Picker,
  Uploader,
  Icon,
  Col,
  Row,
  PullRefresh,
  List,
  Tabbar,
  TabbarItem,
} from 'vant';


// import "_platform/assets/iconfont/iconfont.css";
import "_platform/assets/styles/mobile/dsfStyle.scss";

import '../directives';

import {DsfMobileLoading,DsfMobileEmptyView,DsfMobileButtonNav,DsfIcon} from '../common';

import { DsfMobilePage } from "./page";

import { DsfMobileDataGrid } from "./list";

import { DsfMobileTextBox } from "./input";
import { DsfMobileRow, DsfMobileCol } from './layout';

//
import layer from './layer/layer';
dsf.mix(dsf.layer, { 'mobile': layer });




function install(Vue) {
  Vue.use(Field);
  Vue.use(Popup);
  Vue.use(Dialog);
  Vue.use(Image);
  Vue.use(Grid);
  Vue.use(ImagePreview);
  Vue.use(Loading);
  Vue.use(Picker);
  Vue.use(Uploader);
  Vue.use(Icon);
  Vue.use(Col);
  Vue.use(Row);
  Vue.use(PullRefresh);
  Vue.use(List);
  Vue.use(Tabbar);
  Vue.use(TabbarItem);


  Vue.component(DsfMobilePage.name, DsfMobilePage);
  Vue.component(DsfMobileRow.name, DsfMobileRow);
  Vue.component(DsfMobileCol.name, DsfMobileCol);
  Vue.component(DsfMobileTextBox.name, DsfMobileTextBox);
  Vue.component(DsfMobileDataGrid.name, DsfMobileDataGrid);
  Vue.component(DsfMobileLoading.name,DsfMobileLoading);
  Vue.component(DsfMobileEmptyView.name,DsfMobileEmptyView);
  Vue.component(DsfMobileButtonNav.name,DsfMobileButtonNav);
  Vue.component(DsfIcon.name,DsfIcon);
}

if (typeof window !== "undefined" && window.Vue) {
  install(window.Vue);
}
export {
  install,
  DsfMobilePage,
  DsfMobileTextBox,
  // DsfMobilePopup
};
export default {
  install
};