<template functional>
  <div class="ds-grid-layout mobile" :style="$options.getStyle(_vm)">
    <template v-for="({name, colspan, rowspan, startColIndex, startRowIndex, index}, index2) in props.areas">
      <div
        :key="name"
        ref="gridAreas"
        :index="index2"
        :grid-index="index"
        :row-index="startRowIndex"
        :col-index="startColIndex"
        :colspan="colspan"
        :rowspan="rowspan"
        :class="['grid-area', name]"
        :style="{'grid-area': name,'min-height':$options.getRowHeight(_vm,startRowIndex,startColIndex,colspan,rowspan)}"
      >
        <slot :name="name"></slot>
      </div>
    </template>
    <template v-if="props.isDesign">
      <template v-for="(item, index) in props.gridTemplate">
        <div :key="'-' + index" :class="['grid-area', 'e-' + index]" :style="{'grid-area': 'e-' + index, 'border': 'none'}"></div>
      </template>
    </template>
  </div>
</template>
<script>
export default {
  name: 'DsfMobileDataGridItemLayout',
  props: {
    isDesign: {
      type: Boolean,
      default: false
    },
    gridTemplate: {
      type: Array,
      default() {
        return []
      }
    },
    gridColumns: {
      type: Array,
      default() {
        return [];
      }
    },
    gridRows: {
      type: Array,
      default() {
        return [];
      }
    },
    areas: {
      type: Array,
      default() {
        return [];
      }
    }
  },
  getTemplate(vm) {
    let template = vm.props.gridTemplate;
    return _.map(template, (v, index) => {
      let surplus = '';
      if (vm.props.isDesign) {
        surplus = ' e-' + index;
      }
      return "'grid-area-" + v.join(" grid-area-") + surplus + "'";
    }).join(" ");
  },
  getStyle(vm) {
    let gridColumns = vm.props.gridColumns;
    let gridRows = vm.props.gridRows;
    if (vm.props.isDesign) {
      gridColumns = JSON.parse(JSON.stringify(gridColumns));
      gridColumns.push('0');
    }
    let style = {
      'height': "100%",
      'grid-template-columns': _.map(gridColumns, (el) => dsf.pxToRem(el)).join(" "),
      'grid-template-areas': vm.$options.getTemplate(vm)
    }
    if (!vm.parent.isDesign && gridRows.length) {
      style['grid-template-rows'] = _.map(gridRows, (el) => dsf.pxToRem(el)).join(" ");
    }
    return style;
  },
  getRowHeight(vm, rowIndex, cellIndex, rowSpan, colSpan) {
    if (vm.parent.isDesign) {
      if (rowSpan > 1 && colSpan > 1) {
        return "auto";
      }
      else {
        return dsf.pxToRem(vm.props.gridRows[rowIndex])
      }
    }
    return "auto";
  }
}
</script>