<template>
  <div class="ds-control ds-page mobile" :class="getCss" :style="pageStyle">
    <!---->
    <div v-if="hasHeader" class="ds-page-head">
      <div ref="head" class="head-inner" :slot-name="slots[1].name">
        <slot :name="slots[1].name"></slot>
      </div>
    </div>
    <div class="ds-page-content" :style="pageContentStyle">
      <!-- <div class="ds-page-left" :style="leftStyle" :slot-name="slots[3].name">
        <slot :name="slots[3].name"></slot>
      </div> -->
      <div class="ds-page-center" :slot-name="slots[0].name">
        <slot :name="slots[0].name">
        </slot>
      </div>
      <!-- <div class="ds-page-right" :style="rightStyle" :slot-name="slots[4].name">
        <slot :name="slots[4].name"></slot>
      </div> -->
    </div>
    <!---->
    <div v-if="hasFooter" class="ds-page-foot">
      <div ref="foot" class="foot-inner" :slot-name="slots[2].name">
        <slot :name="slots[2].name"></slot>
      </div>
    </div>
    <div style="display:none" v-html="customCss"></div>
    <a v-if="!isDesign&&gotoIcon" ref="eleDesgin" v-drag data-flag="true" :title="`${title}`" class="to_desgin" @click="gotoPreview">
      <i class="iconfont icon-shezhi"></i>
    </a>
  </div>
</template>
<script>
import view from "_platform/dsf/mixins/view";
export default dsf.component({
  name: "DsfMobilePage",
  ctrlCaption: "",
  mixins: [view],
  data() {
    return {
      gotoIcon: dsf.config.designer.gotoIcon,
      headHeight: 0,
      footHeight: 0,
      centerHeight: "auto",
      headerStyle: {},
      footerStyle: {},
      leftAndRIghtStyle: {},
      containerHeight: 0,
      containerWidth: 0
    };
  },
  props: {
    formItemLabelWidth: {
      type: Number,
      default: 120
    },
    slots: {
      type: Array,
      default() {
        return [
          { name: "center", controls: [] },
          { name: "head", controls: [] },
          { name: "foot", controls: [] },
          // { name: "left", controls: [] },
          // { name: "right", controls: [] }
        ];
      }
    },
    pageLevel: {
      type: Number,
      default: 1
    },
    isKeepAlive: {
      type: Boolean,
      default: false
    }
  },
  beforeCreate() {
    // console.time("");
  },
  computed: {
    leftViewWidth() {
      let views = this.viewSplit.split(",");
      let leftWidth = views[0] ? parseInt(views[0]) : 0
      return leftWidth
    },
    rightViewWidth() {
      let views = this.viewSplit.split(",");
      let rightWidth = views[1] ? parseInt(views[1]) : 0
      return rightWidth
    },
    leftViewShow() {
      let w = parseFloat(this.leftViewWidth);
      return w != 0;
    },
    rightViewShow() {
      let w = parseFloat(this.rightViewWidth);
      return w != 0;
    },

    splitWidth() {
      if (this.leftRight == 'split-small') {
        return 5;
      }
      else if (this.leftRight == 'split-medium') {
        return 10;
      }
      else if (this.leftRight == 'split-big') {
        return 15;
      }
      else {
        return 0;
      }
    },
    splitWidthHorizontal() {
      if (this.topBottom == 'split-small') {
        return 5;
      }
      else if (this.topBottom == 'split-medium') {
        return 10;
      }
      else if (this.topBottom == 'split-big') {
        return 15;
      }
      else {
        return 0;
      }
    },
    getCss() {
      return [
        ...this.$getClass(),
        (this.fit === '1' || this.fit === true) ? 'fit' : '',
      ];
    },
    pageStyle() {
      var headHeight = this.headHeight;
      var footHeight = this.footHeight;
      if (!this.isDesign) {
        headHeight = headHeight + this.splitWidthHorizontal;
        footHeight = footHeight + this.splitWidthHorizontal;
      }
      return {
        "padding-top": headHeight + "px",
        "padding-bottom": footHeight + "px",
      }
    },
    pageContentStyle() {
      var leftWidth = this.leftViewWidth;
      var rightWidth = this.rightViewWidth;
      if (!this.isDesign) {
        leftWidth = leftWidth > 0 ? leftWidth + this.splitWidth : 0;
        rightWidth = rightWidth > 0 ? rightWidth + this.splitWidth : 0
      }
      return {
        "padding-left": leftWidth + "px",
        "padding-right": rightWidth + "px",
        'width': this.isDesign ? 'auto' : (this.pageWidth == "100%" || !this.pageWidth ? 'auto' : this.pageWidth),
        'margin': "auto",
      }
    },
    leftStyle() {
      let style = {};
      var headHeight = this.headHeight;
      var footHeight = this.footHeight;
      var leftWidth = this.leftViewWidth;
      return {
        "width": leftWidth + "px",
        "bottom": '0px',
        'top': '0px',
        'left': "0px"
      }
    },
    rightStyle() {
      let style = {};
      var headHeight = this.headHeight;
      var footHeight = this.footHeight;
      var rightWidth = this.rightViewWidth;
      return {
        "width": rightWidth + "px",
        "bottom": '0px',
        'top': '0px',
        'right': "0px"
      }
    }
  },
  created() {
  },
  destroyed() {
  },
  beforeDestroy(){
    if(this.$refs.eleDesgin){
      this.$refs.eleDesgin.parentNode&&this.$refs.eleDesgin.parentNode.removeChild(this.$refs.eleDesgin)
    }
  },
  mounted() {
    // console.timeEnd("");
    if (this.isDesign) {
      this.watchCenterHeight();
      this.resize();
    }
    if(this.$refs.eleDesgin){
      document.body.append(this.$refs.eleDesgin)
    }
  },
  updated() {
  },
  methods: {
    watchCenterHeight() {
      // this.resize();
      this.setCenterHeight();
      window.setTimeout(this.watchCenterHeight, 1000);
    },
    setCenterHeight() {
      let head = this.$refs.head ? this.$refs.head : null;
      let foot = this.$refs.foot ? this.$refs.foot : null;
      this.headHeight = head ? $(head).outerHeight() : 0;
      this.footHeight = foot ? $(foot).outerHeight() : 0;
    },
    //
    setleftAndRightStyle() {
      if (this.$el) {
        let style = {};
        let height = this.containerHeight - this.headHeight - this.footHeight
        style.height = height + "px"
        style['margin-top'] = this.headHeight + 'px'
        this.leftAndRIghtStyle = style
      }
    },
    //
    setContainerSize() {
      if (this.$el) {
        let container = $(this.$el.parentNode);
        this.containerHeight = container.innerHeight();
        this.containerWidth = container.innerWidth();
      }
    },
    resize() {
      this.setContainerSize();
      this.setCenterHeight();
    },
    gotoPreview() {
      let isDrag = this.$refs.eleDesgin.getAttribute('data-flag')
      if (isDrag === 'true') {
        let previewUrl = `${dsf.url.getWebPath('~/designer.html#/mobile/page?B=' + this.context.B + '&M=' + this.context.M + '&pname=' + this.name + '&title=' + this.title)}`
        window.open(previewUrl)
      }
    }
  },
  directives: {
    drag: { // 
      inserted: function (el) {
        el.onmousedown = function (ev) {
          el.setAttribute('data-flag', false)
          // XYellefttop
          let sX = ev.clientX - el.offsetLeft
          let sY = ev.clientY - el.offsetTop
          document.onmousemove = function (ev) {
            el.style.left = ev.clientX - sX + 'px'
            el.style.top = ev.clientY - sY + 'px'
          }
          const firstTime = new Date().getTime();
          document.onmouseup = function (event) {
            document.onmousemove = null
            document.onmouseup = null;
            const lastTime = new Date().getTime();
            if (lastTime - firstTime < 200) {
              el.setAttribute('data-flag', true)
            }
          }
        }
      }
    }
  }
});
</script>