<template>
  <div>
    <div class="box-wrap staff-vavate-manage-wrap top">
      <!--  -->
      <wf-process ref="wfProcess" />
      <div class="btn-area">
        <div class="disagree-btn" @click="getSendBackLink"></div>
        <div class="agree-btn" @click="sendAction"></div>
      </div>
      <van-dialog
        v-model="showDialog"
        title=""
        show-cancel-button
        confirm-button-text=""
        class="staffAdviseDialog"
        @confirm="onConfirm"
      >
        <van-field v-model="shyj_value" :placeholder="dialogHolder" :border="true" rows="4" autosize type="textarea" />
      </van-dialog>
    </div>

    <!--  -->
    <wf-last-node ref="wfLastNode" />
    <wf-choose-links ref="wfChooseLinks" />
    <wf-choose-users ref="wfChooseUsers" />
    <wf-Opinion ref="wfOpinion" />
  </div>
</template>

<script>
import WFButtons from "./buttons.mixin.js";
export default {
  name: "FlowDemo",
  mixins: [WFButtons],
  data() {
    return {
      itemData: {},
      showDialog: false,
      dialogHolder: "",
      shyj_value: "", //
      btnDir: {
        1: "",
        0: "",
      },
      pageTitle: "",
    };
  },
  created() {
    this.getDetail();
  },
  methods: {
    getDetail() {
      // 
      this.sWFID = "test.wf.wf01";
      this.sPID = dsf.url.queryString("sPID");
      this.sCurLinkID = dsf.url.queryString("sCurLinkID");
      this.getProcessInfo();
    },
    popDialog(cate) {
      this.btn_type = cate;
      this.dialogHolder = this.btnDir[cate];
      this.showDialog = true;
    },
    onConfirm() {
      if (!this.shyj_value) {
        this.shyj_value = this.dialogHolder;
      }
      let _this = this;
      let param = {
        qjdId: this.itemData.teas_xyxt_qjd_id,
        shyj: this.shyj_value,
        type: this.btn_type,
      };
      let url = "teas/mobile/qjsq/sp";
      this.dsf.http
        .get(url, param)
        .done(function (args) {
          console.log(args);
          if (args.success) {
            _this.dsf.layer.message(`${args.message}`);
            _this.$router.go(-1);
          } else {
            _this.dsf.layer.message(`${args.message}`);
          }
        })
        .error(function (error) {
          _this.dsf.layer.message("");
          console.log(`${url}  :${error}`);
        });
    },
    previewFile(zyfj) {
      let file = JSON.parse(zyfj)[0];
      let url = file.relativePath;
      if (!file.id) {
        return;
      }
      this.$router.push({
        name: "bufferPage",
        params: {
          url: url,
          text: file.originalFileName,
          id: file.id,
        },
      });
    },
  },
};
</script>
