<template>
  <div class="ds-control ds-datagrid mobile" style="height:100%">
    <div class="ds-datagrid-wrap">
      <template v-if="isDesign">
        <div class="ds-datagrid-item">
          <slot name="data-item">
            <DsfMobilGridLayout :areas="areas" :is-design="isDesign" :design-only-slot="true" :grid-rows="layout.heights" :grid-columns="layout.widths" :grid-template="layout.matrix">
              <template v-for="area in areas" v-slot:[area.name]>
                <div :key="area.name" :slot-name="area.name" class="grid-area-content">
                  <slot :name="area.name"></slot>
                </div>
              </template>
            </DsfMobilGridLayout>
          </slot>
        </div>
        <div v-show="rangeWrap.show" :style="{height:rangeWrap.height+'px',width:rangeWrap.width+'px',top:rangeWrap.top+'px',left:rangeWrap.left+'px'}" class="selected-range"></div>
      </template>
      <template v-else>
        <van-list v-model="loading" :finished="finished" :finished-text="finishedText" @load="onLoad">
          <van-pull-refresh v-model="refreshing" style="height:100%" @refresh="onRefresh">
            <div v-for="(row,$index) in value" :key="row._id||dsf.uuid()" class="ds-datagrid-item">
              <slot name="data-item">
                <DsfMobilGridLayout :areas="areas" :is-design="isDesign" :design-only-slot="true" :grid-rows="layout.heights" :grid-columns="layout.widths" :grid-template="layout.matrix">
                  <template v-for="area in areas" v-slot:[area.name]>
                    <div :key="area.name" style="border:1px solid red" class="grid-area-content center middle">
                      <div class="ds-multi-ellipsis-2">
                        <slot :name="area.name" v-bind="{'row':row,'$index':$index,'_self':_self}"></slot>
                      </div>
                    </div>
                  </template>
                </DsfMobilGridLayout>
              </slot>
            </div>
          </van-pull-refresh>
        </van-list>
      </template>
    </div>
  </div>
</template>
<script>
import layout from '_platform/dsf/mixins/layout';
import DsfMobilGridLayout from './mobileDataGridItemLayout'
export default dsf.component({
  name: 'DsfMobileDataGrid',
  ctrlCaption: "",
  mixins: [layout],
  components: { DsfMobilGridLayout },
  data() {
    return {
      rangeWrap: {
        show: false,
        width: 0,
        height: 0,
        top: 0,
        left: 0,
        selected: []
      },
      loading: false,
      finished: false,
      refreshing: false,
    }
  },
  props: {
    itemHeight: {
      type: Number,
      default: 60
    },
    value: {
      type: Array,
      default() {
        return [];
      }
    },
    finishedText: {
      type: String,
      default: ""
    },
    searchInfo: {
      type: Array,
      default() {
        return []
      }
    },
    slots: {
      type: Array,
      default() {
        return [
          {
            name: "default",
            controls: []
          },
          {
            name: "data-item",
            controls: [],
            scope: "scope",
            dataKey: "row",
            repeat: true
          }
        ]
      }
    },
    layout: {
      type: Object,
      default() {
        return {
          matrix: [
            [1, 2, 3],
            [5, 6, 7],
            [9, 10, 11],
          ],
          widths: [
            "33.3333%",
            "33.3333%",
            "33.3333%"
          ],
          heights: [
            "46px",
            "46px",
            "46px"
          ]
        }
      }
    }
  },
  computed: {
    areas() {
      let layout = this.layout;
      let obj = {};
      _.each(layout.matrix, (el, i1) => {
        _.each(el, (index, i2) => {
          let name = 'grid-area-' + index;
          if (!obj[name]) {
            obj[name] = {
              name,
              index,
              colspan: 1,
              rowspan: 1,
              startColIndex: i2,
              startRowIndex: i1,
              endColIndex: i2,
              endRowIndex: i1
            }
          } else {
            if (i2 > 0 && obj[name].endColIndex < i2 && el[i2 - 1] == index) {
              obj[name].colspan++;
              obj[name].endColIndex++;
            }
            if (i1 > 0 && obj[name].endColIndex == i2 && obj[name].endRowIndex < i1 && layout.matrix[i1 - 1][i2] == index) {
              obj[name].rowspan++;
              obj[name].endRowIndex++;
            }
          }
        });
      });
      return Object.values(obj);
    }
  },
  created() {
    // createGridContentArea.call(this)
    if (!this.isDesign) {
      for (let i = 0; i < 10; i++) {
        this.value.push({
          "_id": "row" + i,
          "_.textbox2": ""
        })
      }

    }
  },
  mounted() {
    // window.a = this
  },
  methods: {
    onLoad() {
      this.finished = true;
    },
    onRefresh() {
      if (this.value.length > 100) {
        this.value.splice(99, this.value.length);
      }
      window.setTimeout(() => {
        this.refreshing = false;
      }, 2000);
    }
  },
  watch: {
    // gridTemplate:{
    //   handler(v){
    //     let 
    //   },
    //   immediate: true
    // },
    areas: {
      handler(v) {
        let slotMap = _.keyBy(this.slots, (el) => el.name);
        dsf.array.removeWith(this.slots, (el) => el.name.indexOf("grid-area-") == 0);
        _.each(this.areas, (area) => {
          let slot = {
            name: area.name,
            controls: [],
            scope: "scope",
            dataKey: "row",
            unique: "_id",
            repeat: true,
            textProxy: true
          }
          let areaSlot = slotMap[slot.name];
          slot.controls = areaSlot ? areaSlot.controls : [];
          this.slots.push(slot);
        });
        // console.log(this.slots)
      },
      immediate: true
    }
  },
  design: {
    isMask: false,
    metadata: {
      isDeep: false,
      create(root) {
        let childMetaData = [];
        dsf.designer.recursionLayoutTree(this, null, null, child => {
          if (child != this) {
            let designOptions = child.$options.design || {};
            let metadataNode = designOptions.metadata || {};
            let result = metadataNode.create && metadataNode.create.call(child, root);
            if (result && result.metadata) {
              childMetaData.push(result);
              let isDeep = dsf.isDef(metadataNode.isDeep) ? metadataNode.isDeep : true;
              if (!isDeep) {
                return false;
              }
            }
          }
        });

        let query = {};
        let searchBox = [], attachSearch = [];
        for (let i = 0; i < this.searchInfo.length; i++) {
          let s = this.searchInfo[i];
          let item = null;
          if (s.type == "0") {
            item = {
              "code": s.code,
              "operator": s.operation,
              "valueKey": "searchValue"
            }
            searchBox.push(item);
          } else {
            item = {
              "code": s.code,
              "operator": s.operation,
              "valueKey": s.code
            }
            attachSearch.push(item);
            s.metadata_fullcode = item.code;
          }
        }
        //
        let seachBoxitem = { "logic": "or", items: [] };
        seachBoxitem.items.push(...searchBox);

        //
        let attachSearchItem = { "logic": "and", items: [] };
        attachSearchItem.items.push(...attachSearch);

        let searchitems = { "logic": "and", items: [] };
        searchitems.items.push(seachBoxitem);
        searchitems.items.push(attachSearchItem);
        // seachBoxitem.items.length > 0 && searchitems.items.push(seachBoxitem);
        // attachSearchItem.items.length > 0 && searchitems.items.push(attachSearchItem);
        let superSearchItem = { "logic": "and", items: [] }
        _.each(this.superSearchInfo, (el) => {
          let code = null;
          let operator = "";
          let valueKey = null
          if (el.searchType == 'enum') {
            let useFields = [];
            if (el?.children?.length > 0) {
              useFields = _.map(el.children, (f) => f.join == 1);
              useFields = useFields.slice(0, 1);
            }
            else {
              useFields = [el];
            }
            if (useFields.length == 1) {
              code = useFields[0].field
              operator = useFields[0].operator;
              valueKey = useFields[0].field;
              if (operator == "in" || operator == "eq") {
                operator = "in"
              }
              let item = {
                code: code,
                operator: (operator == "in" || operator == "eq") ? "in" : operator,
                valueKey: code
              }
              superSearchItem.items.push(item);
            }
          }
          else if (el.searchType == 'text') {
            code = el.code;
            operator = el.operator;
            valueKey = el.code;
            superSearchItem.items.push({
              code: code,
              operator: operator,
              valueKey: valueKey
            });
          }
          else if (el.searchType == 'datetime') {
            let useFields = [];
            if (el?.children?.length > 0) {
              useFields = _.map(el.children, (f) => f.join == 1);
              useFields = useFields.slice(0, 1);
            }
            else {
              useFields = [el];
            }
            if (useFields.length == 1) {
              superSearchItem.items.push({
                code: useFields[0].field,
                operator: useFields[0].operator,
                valueKey: useFields[0].field,
                dataType: "datetime"
              });
            }
          }
        });
        searchitems.items.push(superSearchItem);
        query = searchitems;
        let metadata = {
          query: query,
          browseScope: this.browseScope,
          _self: this
        }
        return [metadata, ...childMetaData];
      }
    },
    //
    contextMenuHandler(evt) {
      let target = findSelfCell.call(this, $(evt.target));
      if (target && target.length) {
        //
        let result = _.findIndex(this.rangeWrap.selected, (c) => {
          return c.gridIndex == target.attr('grid-index');
        });
        if (result < 0) {
          this.downCell = target;
          let cells = getRangeSelectedCells.call(this, this.downCell, null);
          updateRangeWrap.call(this, cells);
        }
        return [
          { text: "", hide: true, handler: mergeCellsHanlder },
          { text: "", disabled: false, handler: splitCellHandler },
          '-',
          { text: "()", disabled: false, handler: insertRowBefore },
          { text: "()", disabled: false, handler: insertRowAfter },
          { text: "()", disabled: false, handler: insertColumnBefore },
          { text: "()", disabled: false, handler: insertColumnAfter },
          '-',
          { text: "", disabled: false, handler: deleteRows },
          { text: "", disabled: false, handler: deleteColumns },
          '-',
          { text: "", disabled: false, handler: resetColWidth },
          { text: "", disabled: false, handler: resetRowHeight }
        ]
      }
    },
    //
    clearContextMemuHandler() {
      this.isDown = false;
      clearRangeWrap.call(this);
    },
    //mousedown,mousedown
    clearMouseDownHandler() {
      this.isDown = false;
      clearRangeWrap.call(this);
    },
    //down
    mouseDownHandler(evt) {
      if (this.isDesign && evt.button === 0) {
        this.isDown = true;
        this.downCell = findSelfCell.call(this, $(evt.target));
        let cells = getRangeSelectedCells.call(this, this.downCell, null);
        updateRangeWrap.call(this, cells);
        evt.stopPropagation();
      }
    },
    //
    mouseMoveHandler(evt) {
      if (this.isDown) {
        let overCell = findSelfCell.call(this, $(evt.target));
        if (overCell) {
          let cells = getRangeSelectedCells.call(this, this.downCell, overCell);
          updateRangeWrap.call(this, cells);
          evt.stopPropagation();
        }
      }
    },
    //up
    mouseUpHandler() {
      this.isDown = false;
    }
  }
});



/**
 * 
 */
function updateRangeWrap(cells) {
  if (!this.isDesign) {
    return;
  }
  if (!cells || !cells.length) {
    return;
  }
  let top = [];
  let left = [];
  let bottom = [];
  let right = [];
  let selected = cells.map(ce => {
    const cePosition = ce.position();
    top.push(cePosition.top);
    left.push(cePosition.left);
    bottom.push(cePosition.top + ce.height());
    right.push(cePosition.left + ce.width());
    let gridIndex = +ce.attr('grid-index');
    return {
      name: 'grid-area-' + gridIndex,
      gridIndex,
      rowIndex: +ce.attr('row-index'),
      colIndex: +ce.attr('col-index')
    }
  });
  top = Math.min(...top);
  left = Math.min(...left);
  bottom = Math.max(...bottom);
  right = Math.max(...right);
  this.rangeWrap = {
    show: true,
    width: right - left,
    height: bottom - top - 1,
    top: top - 4,
    left: left - 4,
    selected
  }
}
/**
 * 
 * 
 */
function getRangeSelectedCells(start, end) {
  if (end && start.get(0) != end.get(0)) {
    let { minRowIndex, maxRowIndex, minColIndex, maxColIndex } = getRangeRect.call(this, start, end);
    return getRangeCells.call(this, minRowIndex, maxRowIndex, minColIndex, maxColIndex);
  } else {
    return [$(start)];
  }
}

/**
 * 
 * 
 */
function getRangeRect(start, end) {
  let endRowIndex = +end.attr('row-index');
  let endColIndex = +end.attr('col-index');
  let endRowspan = +end.attr('rowspan') - 1;
  let endColspan = +end.attr('colspan') - 1;
  let startRowIndex = +start.attr('row-index');
  let startColIndex = +start.attr('col-index');
  let startRowspan = +start.attr('rowspan') - 1;
  let startColspan = +start.attr('colspan') - 1;
  let minRowIndex = Math.min(startRowIndex, endRowIndex);
  let maxRowIndex = Math.max(startRowIndex + startRowspan, endRowIndex + endRowspan);
  let minColIndex = Math.min(startColIndex, endColIndex);
  let maxColIndex = Math.max(startColIndex + startColspan, endColIndex + endColspan);
  return checkRange.call(this, minRowIndex, maxRowIndex, minColIndex, maxColIndex);
}

/**
 * 
 * # 
 * # 
 */
function getRangeCells(minRowIndex, maxRowIndex, minColIndex, maxColIndex) {
  let selected = [];
  for (let i = minRowIndex; i <= maxRowIndex; i++) {
    for (let j = minColIndex; j <= maxColIndex; j++) {
      let $el = getCellByIndex.call(this, i, j);
      if ($el) {
        selected.push($el);
      }
    }
  }
  return selected;
}

/**
 * 
 */
function checkRange(minRowIndex2, maxRowIndex2, minColIndex2, maxColIndex2) {
  let cells = getRangeCells.call(this, minRowIndex2, maxRowIndex2, minColIndex2, maxColIndex2);
  let endRowIndexs = [], endColIndexs = [], startRowIndexs = [], startColIndexs = [];
  cells.forEach(ce => {
    startRowIndexs.push(+ce.attr('row-index'));
    startColIndexs.push(+ce.attr('col-index'));
    endRowIndexs.push(+ce.attr('row-index') + +ce.attr('rowspan') - 1);
    endColIndexs.push(+ce.attr('col-index') + +ce.attr('colspan') - 1);
  });
  let minRowIndex = Math.min(...startRowIndexs);
  let maxRowIndex = Math.max(...endRowIndexs);
  let minColIndex = Math.min(...startColIndexs);
  let maxColIndex = Math.max(...endColIndexs);
  if (minRowIndex == minRowIndex2 &&
    maxRowIndex == maxRowIndex2 &&
    minColIndex == minColIndex2 &&
    maxColIndex == maxColIndex2) {
    return { minRowIndex, maxRowIndex, minColIndex, maxColIndex };
  } else {
    return checkRange.call(this, minRowIndex, maxRowIndex, minColIndex, maxColIndex);
  }
}

/**
 * 
 */
function getCellByIndex(rowIndex, colIndex) {
  let allDiv = this.$refs.gridAreas;
  if (!allDiv || !allDiv.length) {
    return null;
  }
  let name = 'grid-area-' + this.layout.matrix[rowIndex][colIndex];
  for (let i = 0; i < allDiv.length; i++) {
    if ($(allDiv[i]).hasClass(name)) {
      return $(allDiv[i]);
    }
  }
  return null;
}

/**
 * 
 */
function getCellListByIndexs(indexs) {
  let cells = [];
  indexs.forEach(({ rowIndex, colIndex }) => {
    let cell = getCellByIndex.call(this, rowIndex, colIndex);
    if (cell) {
      cells.push(cell);
    }
  });
  return cells;
}

/**
 * 
 */
function clearRangeWrap() {
  this.downCell = null;
  this.rangeWrap = {
    show: false,
    width: 0,
    height: 0,
    top: -10,
    left: -10,
    selected: []
  }
}

// 
function findSelfCell(cells) {
  for (let i = 0; i < cells.length; i++) {
    let cell = cells.eq(i)
    if (!cell.hasClass('grid-area')) {
      cell = cell.closest('.grid-area');
    }
    let table = cell.closest('.ds-datagrid');
    if (this.$el === table.get(0)) {
      return cell;
    }
  }
  return null;
}

//
function mergeCells(startRowIndex, endRowIndex, startColIndex, endColIndex) {
  let layout = JSON.parse(JSON.stringify(this.layout));
  let index = layout.matrix[startRowIndex][startColIndex];
  for (let i = startRowIndex; i <= endRowIndex; i++) {
    for (let j = startColIndex; j <= endColIndex; j++) {
      layout.matrix[i][j] = index;
    }
  }
  this.$emit('update:layout', layout);
  // 
  this.$nextTick(() => {
    let selectCells = getCellListByIndexs.call(this, this.rangeWrap.selected);
    updateRangeWrap.call(this, selectCells);
  });
}

// 
function getMergedCells(cells) {
  let arr = [];
  cells.forEach(el => {
    let v = el.get ? el : $(el);
    let rowspan = +v.attr('rowspan');
    let colspan = +v.attr('colspan');
    if (rowspan > 1 || colspan > 1) {
      arr.push(v);
    }
  });
  return arr;
}

//
function splitCell(cells) {
  let layout = JSON.parse(JSON.stringify(this.layout));
  cells.forEach(cell => {
    let rowIndex = +cell.attr('row-index');
    let colIndex = +cell.attr('col-index');
    let colspan = +cell.attr('colspan') - 1;
    let rowspan = +cell.attr('rowspan') - 1;
    for (let i = rowIndex, l = rowIndex + rowspan; i <= l; i++) {
      for (let j = colIndex, m = colIndex + colspan; j <= m; j++) {
        layout.matrix[i][j] = i * layout.widths.length + j + 1;
      }
    }
  });
  this.$emit('update:layout', layout);
  // 
  this.$nextTick(() => {
    let selectCells = getCellListByIndexs.call(this, this.rangeWrap.selected);
    updateRangeWrap.call(this, selectCells);
  });
}

// rowscols
function getTableRect(rows, cols) {
  return Array(rows).fill(1).map((v, r) => {
    return Array(cols).fill(1).map((v, c) => {
      return c + 1 + r * cols;
    });
  });
}

// rowIndex
function addRow(rowIndex) {
  let layout = JSON.parse(JSON.stringify(this.layout));
  const emptyTable = getTableRect(layout.matrix.length + 1, layout.widths.length);
  getMergedCells(this.$refs.gridAreas).forEach(cell => {
    let rowStartIndex = +cell.attr('row-index');
    let rowEndIndex = +cell.attr('row-index') + +cell.attr('rowspan') - 1;
    let colStartIndex = +cell.attr('col-index');
    let colEndIndex = +cell.attr('col-index') + +cell.attr('colspan') - 1;
    let index;
    // 
    if (rowIndex <= rowEndIndex && rowIndex > rowStartIndex) {
      rowEndIndex++;
      index = rowStartIndex * (layout.widths.length) + colStartIndex + 1;
    }
    // 
    else if (rowIndex > rowEndIndex) {
      index = rowStartIndex * (layout.widths.length) + colStartIndex + 1;
    }
    // 
    else {
      rowStartIndex++;
      rowEndIndex++;
      index = rowStartIndex * (layout.widths.length) + colStartIndex + 1;
    }
    for (let i = rowStartIndex; i <= rowEndIndex; i++) {
      for (let j = colStartIndex; j <= colEndIndex; j++) {
        emptyTable[i][j] = index;
      }
    }
  });
  layout.matrix = emptyTable;
  layout.heights.splice(rowIndex, 0, '46px');
  this.$emit('update:layout', layout);
  // 
  this.$nextTick(() => {
    let selectCells = getCellListByIndexs.call(this, this.rangeWrap.selected.map(v => {
      if (rowIndex > v.rowIndex) {
        return v;
      } else {
        return {
          rowIndex: v.rowIndex + 1,
          colIndex: v.colIndex
        }
      }
    }));
    updateRangeWrap.call(this, selectCells);
  });
}
// colIndex
function addCol(colIndex) {
  let layout = JSON.parse(JSON.stringify(this.layout));
  const emptyTable = getTableRect(layout.matrix.length, layout.widths.length + 1);
  getMergedCells(this.$refs.gridAreas).forEach(cell => {
    let rowStartIndex = +cell.attr('row-index');
    let rowEndIndex = +cell.attr('row-index') + +cell.attr('rowspan') - 1;
    let colStartIndex = +cell.attr('col-index');
    let colEndIndex = +cell.attr('col-index') + +cell.attr('colspan') - 1;
    let index;
    // 
    if (colIndex <= colEndIndex && colIndex > colStartIndex) {
      index = rowStartIndex * (layout.widths.length + 1) + colStartIndex + 1;
      colEndIndex++;
    }
    // 
    else if (colIndex > colEndIndex) {
      index = rowStartIndex * (layout.widths.length + 1) + colStartIndex + 1;
    }
    // 
    else {
      colStartIndex++;
      colEndIndex++;
      index = rowStartIndex * (layout.widths.length + 1) + colStartIndex + 1;
    }
    for (let i = rowStartIndex; i <= rowEndIndex; i++) {
      for (let j = colStartIndex; j <= colEndIndex; j++) {
        emptyTable[i][j] = index;
      }
    }
  });
  layout.matrix = emptyTable;
  layout.widths = new Array(emptyTable[0].length).fill(
    dsf.pxToRem(+(100 / (layout.widths.length + 1)).toFixed(3) + "%")
  );
  this.$emit('update:layout', layout);
  // 
  this.$nextTick(() => {
    let selectCells = getCellListByIndexs.call(this, this.rangeWrap.selected.map(v => {
      if (colIndex > v.colIndex) {
        return v;
      } else {
        return {
          rowIndex: v.rowIndex,
          colIndex: v.colIndex + 1
        }
      }
    }));
    updateRangeWrap.call(this, selectCells);
  });
}

/************************/
//
function mergeCellsHanlder() {
  let cells = getCellListByIndexs.call(this, this.rangeWrap.selected);
  let start = cells[0];
  let end = cells[cells.length - 1];
  let endRowIndex = +end.attr('row-index');
  let endColIndex = +end.attr('col-index');
  let rowspan = +end.attr('rowspan');
  let colspan = +end.attr('colspan');
  let startRowIndex = +start.attr('row-index');
  let startColIndex = +start.attr('col-index');
  mergeCells.call(this, startRowIndex, endRowIndex + rowspan - 1, startColIndex, endColIndex + colspan - 1);
}
//
function splitCellHandler() {
  let selectCells = getCellListByIndexs.call(this, this.rangeWrap.selected);
  let cells = getMergedCells.call(this, selectCells);
  splitCell.call(this, cells);
}
//
function insertRowBefore() {
  let cells = getCellListByIndexs.call(this, this.rangeWrap.selected);
  let cell = cells[0];
  let rowIndex = +cell.attr('row-index');
  addRow.call(this, rowIndex);
}
//
function insertRowAfter() {
  let cells = getCellListByIndexs.call(this, this.rangeWrap.selected);
  let cell = cells[cells.length - 1];
  let rowIndex = +cell.attr('row-index') + +cell.attr('rowspan');
  addRow.call(this, rowIndex);
}
//
function insertColumnBefore() {
  let cells = getCellListByIndexs.call(this, this.rangeWrap.selected);
  let cell = cells[0];
  let colIndex = +cell.attr('col-index');
  addCol.call(this, colIndex);
}
//
function insertColumnAfter() {
  let cells = getCellListByIndexs.call(this, this.rangeWrap.selected);
  let cell = cells[cells.length - 1];
  let colIndex = +cell.attr('col-index') + +cell.attr('colspan');
  addCol.call(this, colIndex);
}
//
function deleteRows() {
  let cells = getCellListByIndexs.call(this, this.rangeWrap.selected);
  let start = cells[0];
  let end = cells[cells.length - 1];
  let minRowIndex = +start.attr('row-index');
  let maxRowIndex = +end.attr('row-index') + +end.attr('rowspan') - 1;
  let deleteNum = maxRowIndex - minRowIndex + 1;
  let layout = JSON.parse(JSON.stringify(this.layout));
  layout.matrix.splice(minRowIndex, deleteNum);
  layout.heights.splice(minRowIndex, deleteNum);
  this.$emit('update:layout', layout);
}
//
function deleteColumns() {
  let cells = getCellListByIndexs.call(this, this.rangeWrap.selected);
  let start = cells[0];
  let end = cells[cells.length - 1];
  let minColIndex = +start.attr('col-index');
  let maxColIndex = +end.attr('col-index') + +end.attr('colspan') - 1;
  let deleteNum = maxColIndex - minColIndex + 1;
  let layout = JSON.parse(JSON.stringify(this.layout));
  this.layout.matrix.forEach((rows, index) => {
    layout.matrix[index].splice(minColIndex, deleteNum);
  });
  layout.widths = new Array(layout.matrix[0].length).fill(
    dsf.pxToRem(+(100 / layout.matrix[0].length).toFixed(3) + "%")
  );
  this.$emit('update:layout', layout);
}
// 
function resetColWidth() {
  let layout = JSON.parse(JSON.stringify(this.layout));
  layout.widths = new Array(layout.widths.length).fill(
    dsf.pxToRem(+(100 / layout.widths.length).toFixed(3) + "%")
  );
  this.$emit('update:layout', layout);
  // 
  this.$nextTick(() => {
    let selectCells = getCellListByIndexs.call(this, this.rangeWrap.selected);
    updateRangeWrap.call(this, selectCells);
  });
}
// 
function resetRowHeight() {
  let layout = JSON.parse(JSON.stringify(this.layout));
  layout.heights = new Array(layout.heights.length).fill('46px');
  this.$emit('update:layout', layout);
  // 
  this.$nextTick(() => {
    let selectCells = getCellListByIndexs.call(this, this.rangeWrap.selected);
    updateRangeWrap.call(this, selectCells);
  });
}
/************************/
</script>