<template>
  <div class="ds-control ds-form-item ds-textbox mobile" :class="getCss">
    <template v-if="simple&&readOnly&&!isDesign">
      <span>{{ value|proxy(formatter) }}</span>
      <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
    </template>
    <template v-else>
      <van-field v-if="!readOnly" v-model="inputValue" :placeholder="placeholder" :type="inputType" :disabled="disabled" :label="(showLabel&&label?label:'')" :required="required" :input-align="inputAlign"></van-field>
      <van-field v-if="readOnly" v-model="inputValue" :placeholder="placeholder" :type="inputType" :disabled="disabled" :label="(showLabel&&label?label:'')" :input-align="'left'"></van-field>
    </template>
  </div>
</template>
<script>
import formControl from "_platform/dsf/mixins/formControl";
export default dsf.component({
  name: "DsfMobileTextbox",
  mixins: [formControl],
  ctrlCaption: "",
  props: {
    ishideCon: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: ""
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("text-meta-data");
      }
    },
    inputAlign: {
      type: String,
      default: 'right'
    }
  },
  computed: {
    inputType() {
      return this.ishideCon ? "password" : "text";
    },
    inputValue: {
      get() {
        return this.value;
      },
      set(v) {
        this.emitValueChange(v);
      }
    }
  },
  methods: {
  }
})
</script>