import wfLastNode from "./lastNodeDialog";
import wfChooseLinks from "./chooseLinksDialog.vue";
import wfChooseUsers from "./chooseUsersDialog.vue";
import wfProcess from "./flowProcess.vue";
import wfOpinion from "./opinionDialog.vue";
export default {
  components: {
    wfLastNode,
    wfChooseLinks,
    wfChooseUsers,
    wfProcess,
    wfOpinion
  },
  data() {
    return {
      sWFID: '',
      sObjectID: '',
      sObjectName: '',
      sPID: '',
      sCurLinkID: '',
      requestID: null,
      loader: null,
      processInfo: []
    }
  },
  methods: {
    loading(str) {
      if (!this.loader)
        this.loader = dsf.layer.loading(str || '');
    },
    closeLoading(errorTxt) {
      dsf.layer.closeLoading(this.loader);
      this.loader = null;
      if (errorTxt) {
        dsf.layer.message(errorTxt, false);
      }
    },
    sendAction() {
      if (this.sWFID) {
        this.loading();

        let url = "",
          param = {};
        if (!this.sPID) {
          // 
          url = "wfr/startSend";
          param = {
            sWFID: this.sWFID,
            sObjectID: this.sObjectID,
            sObjectName: this.sObjectName,
            sCurNodeID: "",
            sExInfos: ""
          }
        } else {
          // 
          url = "wfr/send";
          param = {
            sPID: this.sPID,
            sCurLinkID: this.sCurLinkID,
            sExInfos: ""
          }
        }

        // 
        param.sOpinion = JSON.stringify({
          'Content': '',
          'Attachments': []
        })

        this.requestID = null;
        dsf.http.post(url, param)
          .then(response => {
            if (response.data.state == 20000 && response.data.data.RequestID) {
              this.wFType(response, []);
            } else {
              dsf.layer.message(`${response.data.message}`);
            }
          })
          .catch(response => {
            dsf.layer.message(`${url}`);
          });
      }
    },
    wFType(res, layerBoxs) {
      if (res.data.state == 20000) {
        let data = res.data.data,
          code = data.Code;
        this.requestID = data.RequestID;
        if (code == 20000 || code == 20004 || code == 20005) {
          //  ||  || 
          for (let i in layerBoxs) {
            layerBoxs[i].show = false;
          }
          this.$refs.wfLastNode.showFun(data, () => {
            this.$router.go(-1);
          })
        } else if (code == 81000) {
          // 
          this.$refs.wfChooseLinks.showFun(data, (d) => {
            layerBoxs.push(this.$refs.wfChooseLinks);
            this.flowSendNextRequest("wfr/sendToLine", {
              sRequestID: this.requestID,
              sLineID: d.LineID
            }, layerBoxs);
          })
        } else if (code == 82000) {
          // 
          this.$refs.wfChooseUsers.showFun(data, (d, txt, files) => {
            layerBoxs.push(this.$refs.wfChooseUsers);
            this.flowSendNextRequest("wfr/sendToReceivers", {
              sRequestID: this.requestID,
              sReceivers: d,
              sOpinion: JSON.stringify({
                'Content': txt,
                'Attachments': files
              })
            }, layerBoxs);
          })
        } else {
          let errorTxt = null;
          switch (code) {
            case 91000:
              errorTxt = ""
              break;
            case 92000:
              errorTxt = ""
              break;
            case 92001:
              errorTxt = ""
              break;
            case 92002:
              errorTxt = ""
              break;
            case 93000:
              errorTxt = ""
              break;
            case 93001:
              errorTxt = ""
              break;
            case 93002:
              errorTxt = ""
              break;
            case 94001:
              errorTxt = ""
              break;
            case 95001:
              errorTxt = ""
              break;
            case 95002:
              errorTxt = ""
              break;
            case 99000:
              errorTxt = ""
              break;
          }
          dsf.layer.alert({
            title: "",
            message: (errorTxt || ''),
            confirmButtonText: ""
          }, () => {
            this.$router.go(-1);
          });
        }
      }
    },
    flowSendNextRequest(url, param, layerBoxs) {
      this.loading();
      let errorTxt = "";
      dsf.http.post(url, param)
        .then(response => {
          if (response.data.state == 20000) {
            this.wFType(response, layerBoxs);
          } else {
            dsf.layer.message(`${response.data.message}`);
          }
        })
        .catch(() => {
          dsf.layer.message(`${url}`);
        });
    },
    getProcessInfo(layer) {
      if (!layer) {
        layer = dsf.layer.loading();
      }
      dsf.http.get('wfr/getProcessInfoByPID', {
        sPID: this.sPID
      })
        .then(response => {
          if (response.data.success) {
            this.$refs.wfProcess.setData(response.data.data)
          } else {
            dsf.layer.message(`${response.data.message}`);
          }
        })
        .catch(() => {
          dsf.layer.message("");
        });
    },
    getSendBackLink() {
      this.loading('');
      dsf.http.post('wfr/getSendBackLink', {
        sPID: this.sPID,
        sCurLinkID: this.sCurLinkID,
      })
        .then(response => {
          if (response.data.state == 20000 && response.data.data.BackLinks) {
            let BackLinks = response.data.data.BackLinks;
            if (BackLinks.length == 1) {
              this.sendBack(BackLinks[0]);
            } else {
              let links = {
                SendParams: {
                  Lines: []
                }
              };
              for (let i in BackLinks) {
                let BackLink = BackLinks[i];
                links['SendParams']['Lines'].push({
                  Name: BackLink.NodeName + '-' + BackLink.LinkUser.RUserName,
                  ...BackLink
                })
              }
              this.$refs.wfChooseLinks.showFun(links, (d) => {
                this.sendBack(d, this.$refs.wfChooseLinks)
              })
            }
          } else {
            dsf.layer.message(`${response.data.message}`);
          }
        })
        .catch(response => {
          dsf.layer.message("");
        });
    },
    sendBack(d, layer) {
      this.$refs.wfOpinion.showFun(0, (msg) => {
        this.loading('');
        let param = {
          sPID: this.sPID,
          sCurLinkID: this.sCurLinkID,
          sReason: msg,
          sBackLinkID: d.ID
        }
        dsf.http.post('wfr/sendBack', param)
          .then(response => {
            if (layer)
              layer.show = false;
            this.$refs.wfOpinion.show = false;

            if (response.data.state == 20000 && response.data.data && response.data.data.Code == '21000') {
              dsf.layer.alert({
                message: ' ' + d.NodeName + ' '
              }, () => {
                this.$router.go(-1);
              })
            } else {
              dsf.layer.message(`${response.data.data.Message || ''}`);
            }
          })
          .catch(response => {
            dsf.layer.message("");
          });
      })
    }
  }
}
